  margin-top: 32px;
}

.production {
  padding-top: 93px;
  background-image: url(../images/production-bg.png);
  background-repeat: no-repeat;
  background-size: cover;
}

.production__title {
  text-align: center;
}

.production__wrap {
  display: grid;
}

.production__card {
  display: flex;
  background: #FFFFFF;
  box-shadow: 0px 0px 10px rgba(0, 0, 0, 0.25);
  border-radius: 10px;
}

.production__card-title {
  text-transform: uppercase;
  font-size: 30px;
  color: #000;
}

.contact {
  padding-top: 150px;
  padding-bottom: 78px;
  background-image: url(../images/contact-bg.png);
  background-repeat: no-repeat;
  background-size: cover;
}

.contact__box {
  display: flex;
  justify-content: space-between;
}

.works {
  padding-top: 110px;
}

.works__title {
  text-align: center;
}

.seo {
  padding: 110px 0;
}

.seo__title {
  text-align: center;
  margin-bottom: 34px;
}

.seo__text {
  text-align: center;
}

.footer {
  padding: 55px 0 32px;
  background-color: #003768;
  color: #ededed;
}

.footer-wrap {
  display: flex;
  justify-content: space-between;
}

.footer-wrap__menu {
  display: flex;
}

.footer-wrap__menu li:not(:last-child) {
  margin-right: 25px;
}

.footer-wrap__menu li a {
  font-size: 12px;
  color: #ededed;
}

.footer-wrap__menu li a:hover {
  color: #F6C40D;
  text-decoration: underline;
}

.footer-wrap__text {
  margin-top: 19px;
  font-size: 14px;
  width: 290px;
  max-width: 290px;
}

.footer-wrap__contacts-box {
  text-align: right;
}

.footer-wrap__contacts-box i {
  color: #F6C40D;
  margin-right: 10px;
  margin-left: auto;
}

.footer-wrap__contacts-box .fa-phone {
  transform: rotate(97deg);
}

.footer-wrap__contacts-box-phone {
  color: #f9f9f9;
  font-size: 14px;
  font-weight: 500;
}

.footer-wrap__contacts-box-phone:hover {
  color: #F6C40D;
}

.footer-wrap__contacts-box-mail {
  color: #ededed;
  font-size: 14px;
  font-weight: 400;
}

.footer-wrap__contacts-box-mail:hover {
  color: #F6C40D;
}

/* ------ Media Queries ------ */
/*------1201px  -  1366px-------*/
/* 992-1200px */
@media screen and (max-width: 1200px) {
  .header .menu li {
    margin-right: 20px;
  }
  .header__phone {
    display: none;
  }
}

/* 768-992px */
@media screen and (max-width: 992px) {
  .header .gamburger {
    display: block;
    font-size: 35px;
    color: #fff;
  }
  .header nav {
    overflow: hidden;
  }
  .header .menu {
    transform: translateY(-300%);
    overflow: hidden;
  }
  .header .menu li {
    margin-right: 0;
    margin-top: 25px;
  }
  .header .opened {
    display: flex;
    flex-direction: column;
    position: fixed;
    width: 100%;
    left: 0;
    top: 0;
    padding-top: 70px;
    justify-content: center;
    align-items: center;
    transform: translateY(0);
    transition: all 0.5s ease;
    z-index: 9;
    background-color: #fff;
  }
  .header .open {
    display: block;
    position: absolute;
    right: 20px;
    top: 20px;
    color: #fff;
    opacity: 1;
  }
  .header .close-menu {
    display: none;
  }
  .footer-wrap {
    justify-content: space-around;
  }
  .footer-wrap__menu {
    display: none;
  }
  .footer-rights {
    margin-top: 50px;
  }
}

/* 576-768px */
@media screen and (max-width: 768px) {
  .box-modal {
    width: 100%;
  }
  .modal-form {
    width: 100%;
  }
  .form {
    padding: 10px;
  }
  .form__title {
    font-size: 30px;
  }
  .form__text {
    font-size: 16px;
    width: 100%;
    max-width: 100%;
  }
  .form-control {
    width: 100%;
  }
  .form-check {
    padding-left: 3.5rem;
  }
  .form__btn {
    width: 100%;
  }
  .form-check-label {
    width: 100%;
  }
  .form .check__box {
    left: 0.5em;
  }
}

/* 320-576px */
